.menu {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.menu__container {
  width: 100%;
  &-title {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0, 0, 0, 0);
    opacity: 0;
  }
}

.menu__bottom {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #1e323b;
  color: #fff;
  width: 100%;
  margin-top: 10px;
  &-title {
    padding: 0;
    margin: 0;
    padding-top: 10px;
    padding-bottom: 15px;
    font-size: 22px;
    line-height: 1.5;
  }
  &-phone {
    font-size: 16px;
    padding: 0;
    margin: 0;
    padding-bottom: 15px;
    text-decoration: none;
    color: #fff;
  }
  &-email {
    font-size: 16px;
    padding: 0;
    margin: 0;
    padding-bottom: 15px;
    text-decoration: none;
    color: #fff;
  }
}

@media(min-width: 768px) {
  .menu__bottom {
    display: none;
  }
}