.contact {
  background-color: #f5f5f5;
}

.contact__title {
  padding-top: 40px;
  font-weight: 800;
  font-size: 22px;
  text-align: center;
  &-bottom {
    font-size: 19px;
    padding-top: 20px;
    text-transform: uppercase;
  }
}

.contact__time {
  font-size: 18px;
  padding-bottom: 15px;
}

.contact__wrapper {
  text-align: center;
}

.contact__bottom-phone {
  margin: 0 20px;
  font-size: 18px;
  line-height: 2.8;
  color: #000;
  text-decoration: none;
}

.contact__position {
  padding-top: 5px;
  text-align: center;
}

.contact__bottom-email {
  font-size: 18px;
  color: #000;
  text-decoration: none;
}

@media(min-width: 1280px) {
  .contact__title {
    font-size: 25px;
    text-align: left;
    padding-left: 150px;
    margin: 0;
    padding-top: 32px;
    margin-bottom: 30px;
    &-bottom {
      font-size: 25px;
    }
  }
  .contact__top {
    display: flex;
    flex-wrap: wrap;
  }
  .contact__time {
    padding-top: 15px;
    font-size: 20px;
  }
  .contact__position {
    width: 55%;
    text-align: left;
  }
  .contact__wrapper {
    width: 45%;
  }
  .contact__bottom-phone {
    margin: 0;
    font-size: 20px;
    padding-left: 150px;
  }
  .contact__bottom-email {
    font-size: 20px;
    padding-left: 30px;
  }
}
