input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type='number'],
input[type="number"]:hover,
input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

.form__choice {
  width: 60%;
  margin: 0 auto;
  height: 40px;
  outline: none;
  border: none;
  padding-left: 4px;
  background-color: #efefef;
}

.form__title {
  margin-top: 40px;
}

.form__button {
  font-weight: 800;
  width: 70%;
  height: 40px;
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: 20px;
  background-color: transparent;
  border: 3px solid #966bc7;
  color: #65449b;
  border-radius: 5px;
  position: relative;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: #966bc7;
    color: #fff;
  }
}

@media(min-width: 1280px) {
  .form {
    width: 100%;
    margin-left: 25px;
    margin-right: 20px;
  }
  .form__position {
    display: flex;
    margin-top: 20px;
  }
  .form__clean {
    width: 70%;
    margin-right: 20px;
  }
  .form__title {
    margin-top: 20px;
  }
  .form__lot {
    width: 80%;
    margin-right: 20px;
  }
  .form__rooms {
    width: 80%;
    margin-right: 20px;
  }
  .form__choice {
    width: 100%;
  }
  .form__button {
    margin-top: 55px;
  }
}


/*popup*/

.form__calculat {
  font-size: 18px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: none;
  pointer-events: none;
  text-align: center;
  transition: 0.5s all;
  &-position {
    position: absolute;
    top: 50%;
    left: 50%;
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5);
    transform: translate(-50%, -50%) scale(0);
    background: #efefef;
    width: 50%;
    height: 40%;
    padding: 45px;
    text-transform: uppercase;
    line-height: 2;
    transition: 0.5s all;
    border-radius: 22px;
    font-weight: 600;
  }
  &-area {
    padding-bottom: 20px;
  }
  &-close {
    width: 45px;
    height: 45px;
    margin-right: -50px;
    position: absolute;
    top: 0;
    right: 50px;
    background-image: url('../img/close.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-color: transparent;
    border: none;
    border-radius: 15px;
  }
}

.active-one {
  display: block;
  pointer-events: all;
  transition: 0.5s all;
}

.active {
  transform: translate(-50%, -50%) scale(1);
  transition: 0.5s all;
}

.close__popup {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}