.work__title {
  font-weight: 800;
  font-size: 22px;
  text-align: center;
}

.work__list {
  color: #82828e;
  list-style-type: none;
  margin: 0;
  padding: 0;
  &-item {
    text-align: center;
    padding: 20px 100px;
    line-height: 1.5;
    z-index: 1;
    &--first::before {
      content: '1';
      font-size: 100px;
      color: #dfdfe1;
      z-index: -1;
      position: absolute;
      margin-top: -40px;
      margin-left: -30px;
    }
    &--second::before {
      content: '2';
      font-size: 100px;
      color: #dfdfe1;
      z-index: -1;
      position: absolute;
      margin-top: -40px;
      margin-left: -30px;
    }
    &--three::before {
      content: '3';
      font-size: 100px;
      color: #dfdfe1;
      z-index: -1;
      position: absolute;
      margin-top: -40px;
      margin-left: -30px;
    }
    &--fore::before {
      content: '4';
      font-size: 100px;
      color: #dfdfe1;
      z-index: -1;
      position: absolute;
      margin-top: -40px;
      margin-left: -30px;
    }
    &--five::before {
      content: '5';
      font-size: 100px;
      color: #dfdfe1;
      z-index: -1;
      position: absolute;
      margin-top: -40px;
      margin-left: -30px;
    }
    &--six::before {
      content: '6';
      font-size: 100px;
      color: #dfdfe1;
      z-index: -1;
      position: absolute;
      margin-top: -40px;
      margin-left: -30px;
    }
    &-description {
      padding: 65px 0 0;
    }
  }
}

@media(min-width: 1280px) {
  .work__title {
    text-align: left;
    padding-left: 150px;
    padding-top: 60px;
  }
  .work__list {
    display: flex;
    padding-top: 30px;
    padding-bottom: 40px;
    margin: 0 110px;
    &-item {
      padding: 0 30px;
      width: 100%;
      &--first::before {
        margin-top: 45px;
      }
      &--second::before {
        margin-top: 45px;
      }
      &--three::before {
        margin-top: 45px;
      }
      &--fore::before {
        margin-top: 45px;
      }
      &--five::before {
        margin-top: 45px;
      }
      &--six::before {
        margin-top: 45px;
      }
      &-description {
        padding: 0;
      }
    }
  }
}
