.services {
  padding-top: 30px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  color: #000;
}

.services__title {
  font-weight: 800;
  font-size: 22px;
  line-height: 1.5;
}

.services__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  @media(max-width: 1279px) {
    &-item--color {
      background-color: #f1f1f1;
      padding-bottom: 5px;
    }
  }
  &-item h3 {
    text-transform: uppercase;
  }
  &-title {
    padding-top: 10px;
    font-size: 18px;
    font-weight: 600;
    &-bottom {
      font-size: 18px;
      color: #8d56cb;
      text-decoration: underline;
    }
  }
  &-description {
    padding: 10px 30px;
    font-size: 14px;
    font-weight: 500;
    position: relative;
    text-align: center;
    &::before {
      content: '';
      position: absolute;
      top: 15px;
      margin-left: -20px;
      border: 5px solid #6d2fb6;
      border-radius: 50%;
    }
  }
}

@media (min-width: 1280px) {
  .services__list {
    display: flex;
    margin: 0 120px;
    &-item {
      width: 100%;
      box-shadow: 0 0 4px 4px #e7e7e7;
      margin: 0 20px;
      padding-bottom: 50px;
      position: relative;
      &--bottom {
        margin-top: 160px;
      }
    }
    &-title {

      font-size: 14px;
      &-bottom{
position: absolute;
text-align: center;
left: 40%;
bottom: 0;
      }
    }
  }
  .services__title {
    text-align: left;
    padding-left: 150px;
  }
}
