.wrapper {
  font-size: 18px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  text-align: center;
  transition: 0.5s all;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

.wrapper--show {
  display: block;
}

.form-popup {
  margin: 0 auto;
  padding-bottom: 10px;
  position: absolute;
  top: 10%;
  left: 15%;
  right: 10%;
  background-color: #fff;
  border-radius: 5px;
  max-width: 500px;
  color: #000;
}

.form-popup__title {
  line-height: 1;
  font-size: 30px;
  font-weight: 700;
}

.form-popup__item {
  margin: 0 0 30px 0;
}

.form-popup__label {
  font-size: 18px;
  display: block;
  margin: 0 0 10px 0;
}

.form-popup__input {
  height: 50px;
  padding: 0 20px;
  border-radius: 5px;
  width: 90%;
  font-size: 18px;
  outline: none;
  border: none;
  background-color: #f0f0f2;
  &::placeholder {
    text-align: center;
  }
}

.checkbox__input {
  display: none;
}

.checkbox__label {
  font-size: 16px;
  line-height: 140%;
  display: inline-flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  padding-top: 10px;
  &-link {
    text-decoration: none;
    color: #703eb1;
    font-weight: 700;
  }
  &::before {
    content: '';
    align-self: flex-start;
    flex: 0 0 25px;
    height: 25px;
    background-color: #efefef;
    border-radius: 5px;
    margin-left: 10px;
  }
  &::after {
    content: '';
    width: 30px;
    height: 35px;
    position: absolute;
    top: 8px;
    left: 8px;
    background-image: url('../img/check.svg');
    background-repeat: no-repeat;
    transform: scale(0);
  }
}

.form-popup__input._error {
  box-shadow: 0 0 15px red;
}

.checkbox._error .checkbox__label::before {
  box-shadow: 0 0 15px red;
}

.checkbox__input:checked+.checkbox__label::after {
  transform: scale(1);
}

.form-popup__button {
  width: 70%;
  min-height: 60px;
  background-color: #703eb1;
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
  border-radius: 20px;
  text-transform: uppercase;
  outline: none;
  border: none;
  margin-top: 10px;
  &:hover {
    border: 3px solid #966bc7;
    color: #65449b;
    background-color: #fff;
  }
}
