.main {
  background: linear-gradient(to top, #fff 40%, #f4f4f6 40%);
  text-align: center;
}

.main__more {
  width: 250px;
  height: 45px;
  border-radius: 5px;
  color: #fff;
  border: none;
  background-color: #703eb1;
  cursor: pointer;
  font-size: 17px;
  font-weight: 700;
  &:hover{
    background-color: #fff;
    border: 3px solid #966bc7;
    color: #65449b;
  }
}

.main__order-title {
  font-size: 18px;
  line-height: 1.5;
  padding-top: 20px;
  &-free {
    text-transform: uppercase;
  }
}

.main__wrapper {
  padding-top: 60px;
  padding-left: 15px;
}

.main__title {
  font-weight: 800;
  margin: 0;
  padding-top: 20px;
  font-size: 22px;
  line-height: 1.5;
  &--show {
    display: none;
  }
  @media (min-width: 768px) {
    &--shadow {
      display: none;
    }
    &--show {
      display: block;
    }
  }
}

.main__description {
  font-size: 14px;
  padding-top: 10px;
  padding-bottom: 20px;
  line-height: 1.5;
}

.main__count-wrapper {
  align-self: center;
  margin: 0 30px;
  margin-top: 70px;
  background-color: #fff;
  box-shadow: 0 0 5px 5px #e1e1e3;
  z-index: 1;
}

.main__cartoon {
  display: none;
}

@media(min-width: 1280px) {
  .main {
    text-align: left;
    background: linear-gradient(to top, #fff 15%, #f4f4f6 15%);
    position: relative;
    z-index: 1;
  }
  .main__title {
    padding-top: 110px;
    padding-left: 150px;
  }
  .main__description {
    padding-left: 150px;
  }
  .main__more {
    margin-left: 150px;
  }
  .main__cartoon {
    display: block;
    position: absolute;
    bottom: 90px;
    right: 65px;
    z-index: -1;
  }
  .main__count-wrapper {
    display: flex;
    margin: 120px 150px;
    padding-bottom: 20px;
  }
  .main__order-title {
    padding-left: 25px;
    &--shadow {
      display: none;
    }
    &-free {
      padding-left: 2px;
    }
  }
}
