.navbar {
  padding: 10px 15px;
}

.navbar-position {
  width: 100%;
  margin: 0;
}

.navbar__brand {
  width: 70%;
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  padding: 0;
  &-logo {
    width: 150px;
  }
}

.navbar-toggler {
  width: 25px;
  height: 20px;
  margin-top: 15px;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
  @media (min-width: 768px) {
    display: none;
  }
  &--opened {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 1px;
      right: 0;
      width: 24px;
      height: 1.5px;
      z-index: 2;
      background-color: black;
      box-shadow: 0 8px 0 0 black, 0 16px 0 0 black;
    }
  }
  &--closed {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 6px;
      right: 0;
      width: 24px;
      height: 1.5px;
      z-index: 2;
      background-color: black;
      transform: rotate(135deg);
    }
    &::after {
      content: "";
      position: absolute;
      top: 6px;
      right: 0;
      width: 24px;
      height: 1.5px;
      z-index: 2;
      background-color: black;
      transform: rotate(45deg);
    }
  }
}

.navbar__wrapper {
  width: 100%;
  padding-top: 15px;
  text-align: center;
  @media(max-width: 767px) {
    &--hidden {
      display: none;
    }
  }
}

.navbar__list {
  padding: 0;
  margin: 0;
}

.navbar__position {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

@media(min-width: 768px) {
  .navbar {
    margin: 0 20px;
  }
  .navbar__position {
    flex-wrap: nowrap;
    flex-direction: row;
  }
  .navbar__list {
    display: flex;
    justify-content: center;
    padding: 0;
  }
  .navbar__brand {
    width: 50%;
  }
  .navbar__wrapper {
    width: 100%;
  }
}

@media(min-width: 1280px) {
  .navbar {
    margin: 0 130px;
  }
  .navbar-position {
    margin: 0 85px;
  }
}
