.list__item {
  padding: 15px 0;
  list-style-type: none;
  &-link {
    text-decoration: none;
    color: black;
    &:hover{
      border-bottom: 2px solid #703eb1;
    }
    &:active{
      opacity: 0.2;
    }
  }
}


@media(min-width: 768px) {
  .list__item {
    padding: 0 33px;
  }
}

@media(min-width: 1280px) {
  .list__item {
    padding: 0 56px;
  }
}
