.advantages__title {
  font-weight: 800;
  padding: 20px 0;
  margin: 0;
  text-align: center;
}

.advantages__list {
  margin: 0;
  padding: 0;
  text-align: center;
}

.advantages__item {
  padding: 20px 0;
  margin: 0;
  list-style-type: none;
  position: relative;
  &--people::before {
    content: '';
    position: absolute;
    width: 65px;
    height: 55px;
    background-image: url('../img/people.svg');
    background-repeat: no-repeat;
    margin-left: 90px;
    margin-top: 5px;
  }
  &--dollar::before {
    content: '';
    position: absolute;
    width: 75px;
    height: 55px;
    background-image: url('../img/dollar.svg');
    background-repeat: no-repeat;
    margin-left: 15px;
    margin-top: -5px;
  }
  &--guard::before {
    content: '';
    position: absolute;
    width: 85px;
    height: 55px;
    background-image: url('../img/guard.svg');
    background-repeat: no-repeat;
    margin-left: 55px;
    margin-top: 1px;
  }
  &--percent::before {
    content: '';
    position: absolute;
    width: 75px;
    height: 65px;
    background-image: url('../img/percent.svg');
    background-repeat: no-repeat;
    margin-left: 30px;
    margin-top: -2px;
  }
  &-description {
    padding: 0;
  }
}

@media(min-width: 1280px) {
  .advantages__list {
    display: flex;
    text-align: left;
    margin: 40px 140px;
  }
  .advantages__title {
    text-align: left;
    padding-left: 150px;
  }
  .advantages__item {
    width: 80%;
    border-radius: 5%;
    margin: 0 20px;
    box-shadow: 0 0 4px 4px #eee;
    &-title {
      padding-top: 40px;
      padding-left: 15px;
    }
    &-description {
      padding-left: 15px;
    }
    &--people::before {
      width: 80px;
      height: 70px;
      margin-left: 10px;
      margin-top: 0;
    }
    &--dollar::before {
      width: 100px;
      height: 80px;
      margin-left: -5px;
      margin-top: -15px;
    }
    &--guard::before {
      width: 100px;
      height: 80px;
      margin-left: 10px;
      margin-top: -5px;
    }
    &--percent::before {
      width: 100px;
      height: 90px;
      margin-left: 5px;
      margin-top: -5px;
    }
  }
}
