.about-us {
  text-align: center;

  &-title {
    font-weight: 800;
    padding-top: 26px;
  }

  &-description {
    margin: 0 20px;
  }

  &-cartoon {
    margin-top: 15px;
  }
}

@media(min-width: 1280px) {
  .about-us {
    display: flex;
    text-align: left;
    justify-content: space-between;

    &-description {
      margin: 0;
    }

    &-cartoon {
      margin-top: 70px;
      margin-right: 190px;
    }

  }

  .about-us__position {
    padding-left: 150px;
  }
}
