@media (min-width: 320px) {
  #cookie_notification {
    display: none;
    justify-content: space-between;
    align-items: flex-end;
    position: fixed;
    bottom: -20px;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    padding: 25px;
    background: linear-gradient(to left, #00d0da, #009adc 49%, #0072d8);
    border-radius: 5px;
    box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.4);
    z-index: 2;
  }
  #cookie_notification p {
    margin: 0;
    font-size: 0.6rem;
    line-height: 1.5;
    padding-top: 10px;
    font-weight: 700;
    text-align: left;
    color: #fff;
    position: relative;
  }
  #cookie_notification h2 {
    position: absolute;
    top: -10px;
    color: #fff;
  }
  #cookie_notification.show {
    display: flex;
  }
  .cookie_accept {
    margin: 0 0 10px 15px;
    outline: none;
    cursor: pointer;
    border: none;
    width: 320px;
    height: 50px;
    border-radius: 5%;
    background-color: #703eb1;
    color: #fff;
    text-transform: uppercase;
  }
}

@media (min-width: 768px) {
  #cookie_notification p {
    font-size: 1rem;
  }
}
