.progress {
  padding-top: 40px;
  padding-bottom: 100px;
  text-align: center;
  min-width: 0;
  margin: 0;
}

.slick-track {
  display: flex;
}

.slick-list {
  overflow: hidden;
}

.progress__slider {
  margin: 0 20px;
  margin-top: 40px;
  position: relative;
  z-index: 2;
}

.progress__slider .slick-dots {
  display: flex;
  justify-content: center;
}

.progress__slider .slick-dots li {
  list-style-type: none;
  padding: 0 10px;
}

.progress__slider .slick-dots button {
  font-size: 0;
  width: 10px;
  height: 10px;
  background-color: #652ead;
  border-radius: 50%;
  border: none;
}

.progress__slider .slick-arrow {
  position: absolute;
  z-index: 10;
  top: 100%;
  font-size: 0;
  cursor: pointer;
}

.progress__slider .slick-arrow.slick-prev {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  background-image: url('../img/arrow.svg');
  background-repeat: no-repeat;
  left: 60px;
}

.slick-track {
  cursor: pointer;
}

.progress__slider .slick-arrow.slick-next {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  background-image: url('../img/arrow.svg');
  background-repeat: no-repeat;
  transform: rotate(180deg);
  right: 60px;
}

@media(min-width: 768px) {
  .slick-slide {
    margin: 0 10px;
  }
  .progress__slider .slick-arrow {
    top: 105%;
  }
  .progress__slider .slick-arrow.slick-prev {
    left: 250px;
  }
  .progress__slider .slick-arrow.slick-next {
    right: 250px;
  }
}

@media(min-width: 1280px) {
  .progress {
    text-align: left;
    padding-left: 150px;
    margin-top: 40px;
  }
  .progress__slider {
    margin: 0;
    margin-left: -10px;
    margin-right: 115px;
    margin-top: 30px;
  }
  .progress__slider .slick-arrow {
    top: 102%;
  }
  .progress__slider .slick-arrow.slick-prev {
    margin-left: 200px;
  }
  .progress__slider .slick-arrow.slick-next {
    margin-right: 180px;
  }
}