@media (min-width: 320px) {
  .request {
    position: fixed;
    right: 0;
    top: 20%;
    width: 20px;
    height: 20px;
    transform: rotate(90deg);
    z-index: 2;
  }
  .request__button {
    margin-left: auto;
    color: #fff;
    width: 300px;
    height: 20px;
    transform: rotate(180deg);
    animation: glowing 1300ms infinite;
    outline: none;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    z-index: 2000;
  }
  .request__name {
    font-size: 17px;
  }
  @keyframes glowing {
    0% {
      background-color: #966bc7;
      box-shadow: 0 0 3px #966bc7;
    }
    50% {
      background-color: #da70d6;
      box-shadow: 0 0 10px #da70d6;
    }
    100% {
      background-color: #966bc7;
      box-shadow: 0 0 3px #966bc7;
    }
  }
}

@media (min-width: 768px) {
  .request__button {
    height: 30px;
  }
}

@media (min-width: 1280px) {
  .request {
    top: 22%;
  }
  .request__button {
    height: 50px;
  }
}
