@font-face {
  font-weight: normal;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  font-weight: normal;
  color: black;
  background-color: white;
  background-position: top center;
  background-repeat: no-repeat;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  min-width: 100%;
  height: 100%;
}

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.menu__container {
  width: 100%;
}
.menu__container-title {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
  opacity: 0;
}

.menu__bottom {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: #1e323b;
  color: #fff;
  width: 100%;
  margin-top: 10px;
}
.menu__bottom-title {
  padding: 0;
  margin: 0;
  padding-top: 10px;
  padding-bottom: 15px;
  font-size: 22px;
  line-height: 1.5;
}
.menu__bottom-phone {
  font-size: 16px;
  padding: 0;
  margin: 0;
  padding-bottom: 15px;
  text-decoration: none;
  color: #fff;
}
.menu__bottom-email {
  font-size: 16px;
  padding: 0;
  margin: 0;
  padding-bottom: 15px;
  text-decoration: none;
  color: #fff;
}

@media (min-width: 768px) {
  .menu__bottom {
    display: none;
  }
}
.navbar {
  padding: 10px 15px;
}

.navbar-position {
  width: 100%;
  margin: 0;
}

.navbar__brand {
  width: 70%;
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  padding: 0;
}
.navbar__brand-logo {
  width: 150px;
}

.navbar-toggler {
  width: 25px;
  height: 20px;
  margin-top: 15px;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navbar-toggler {
    display: none;
  }
}
.navbar-toggler--opened {
  position: relative;
}
.navbar-toggler--opened::before {
  content: "";
  position: absolute;
  top: 1px;
  right: 0;
  width: 24px;
  height: 1.5px;
  z-index: 2;
  background-color: black;
  box-shadow: 0 8px 0 0 black, 0 16px 0 0 black;
}
.navbar-toggler--closed {
  position: relative;
}
.navbar-toggler--closed::before {
  content: "";
  position: absolute;
  top: 6px;
  right: 0;
  width: 24px;
  height: 1.5px;
  z-index: 2;
  background-color: black;
  transform: rotate(135deg);
}
.navbar-toggler--closed::after {
  content: "";
  position: absolute;
  top: 6px;
  right: 0;
  width: 24px;
  height: 1.5px;
  z-index: 2;
  background-color: black;
  transform: rotate(45deg);
}

.navbar__wrapper {
  width: 100%;
  padding-top: 15px;
  text-align: center;
}
@media (max-width: 767px) {
  .navbar__wrapper--hidden {
    display: none;
  }
}

.navbar__list {
  padding: 0;
  margin: 0;
}

.navbar__position {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

@media (min-width: 768px) {
  .navbar {
    margin: 0 20px;
  }

  .navbar__position {
    flex-wrap: nowrap;
    flex-direction: row;
  }

  .navbar__list {
    display: flex;
    justify-content: center;
    padding: 0;
  }

  .navbar__brand {
    width: 50%;
  }

  .navbar__wrapper {
    width: 100%;
  }
}
@media (min-width: 1280px) {
  .navbar {
    margin: 0 130px;
  }

  .navbar-position {
    margin: 0 85px;
  }
}
.main {
  background: linear-gradient(to top, #fff 40%, #f4f4f6 40%);
  text-align: center;
}

.main__more {
  width: 250px;
  height: 45px;
  border-radius: 5px;
  color: #fff;
  border: none;
  background-color: #703eb1;
  cursor: pointer;
  font-size: 17px;
  font-weight: 700;
}
.main__more:hover {
  background-color: #fff;
  border: 3px solid #966bc7;
  color: #65449b;
}

.main__order-title {
  font-size: 18px;
  line-height: 1.5;
  padding-top: 20px;
}
.main__order-title-free {
  text-transform: uppercase;
}

.main__wrapper {
  padding-top: 60px;
  padding-left: 15px;
}

.main__title {
  font-weight: 800;
  margin: 0;
  padding-top: 20px;
  font-size: 22px;
  line-height: 1.5;
}
.main__title--show {
  display: none;
}
@media (min-width: 768px) {
  .main__title--shadow {
    display: none;
  }
  .main__title--show {
    display: block;
  }
}

.main__description {
  font-size: 14px;
  padding-top: 10px;
  padding-bottom: 20px;
  line-height: 1.5;
}

.main__count-wrapper {
  align-self: center;
  margin: 0 30px;
  margin-top: 70px;
  background-color: #fff;
  box-shadow: 0 0 5px 5px #e1e1e3;
  z-index: 1;
}

.main__cartoon {
  display: none;
}

@media (min-width: 1280px) {
  .main {
    text-align: left;
    background: linear-gradient(to top, #fff 15%, #f4f4f6 15%);
    position: relative;
    z-index: 1;
  }

  .main__title {
    padding-top: 110px;
    padding-left: 150px;
  }

  .main__description {
    padding-left: 150px;
  }

  .main__more {
    margin-left: 150px;
  }

  .main__cartoon {
    display: block;
    position: absolute;
    bottom: 90px;
    right: 65px;
    z-index: -1;
  }

  .main__count-wrapper {
    display: flex;
    margin: 120px 150px;
    padding-bottom: 20px;
  }

  .main__order-title {
    padding-left: 25px;
  }
  .main__order-title--shadow {
    display: none;
  }
  .main__order-title-free {
    padding-left: 2px;
  }
}
.list__item {
  padding: 15px 0;
  list-style-type: none;
}
.list__item-link {
  text-decoration: none;
  color: black;
}
.list__item-link:hover {
  border-bottom: 2px solid #703eb1;
}
.list__item-link:active {
  opacity: 0.2;
}

@media (min-width: 768px) {
  .list__item {
    padding: 0 33px;
  }
}
@media (min-width: 1280px) {
  .list__item {
    padding: 0 56px;
  }
}
input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type=number],
input[type=number]:hover,
input[type=number]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

.form__choice {
  width: 60%;
  margin: 0 auto;
  height: 40px;
  outline: none;
  border: none;
  padding-left: 4px;
  background-color: #efefef;
}

.form__title {
  margin-top: 40px;
}

.form__button {
  font-weight: 800;
  width: 70%;
  height: 40px;
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: 20px;
  background-color: transparent;
  border: 3px solid #966bc7;
  color: #65449b;
  border-radius: 5px;
  position: relative;
  outline: none;
  cursor: pointer;
}
.form__button:hover {
  background-color: #966bc7;
  color: #fff;
}

@media (min-width: 1280px) {
  .form {
    width: 100%;
    margin-left: 25px;
    margin-right: 20px;
  }

  .form__position {
    display: flex;
    margin-top: 20px;
  }

  .form__clean {
    width: 70%;
    margin-right: 20px;
  }

  .form__title {
    margin-top: 20px;
  }

  .form__lot {
    width: 80%;
    margin-right: 20px;
  }

  .form__rooms {
    width: 80%;
    margin-right: 20px;
  }

  .form__choice {
    width: 100%;
  }

  .form__button {
    margin-top: 55px;
  }
}
/*popup*/
.form__calculat {
  font-size: 18px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: none;
  pointer-events: none;
  text-align: center;
  transition: 0.5s all;
}
.form__calculat-position {
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.5);
  transform: translate(-50%, -50%) scale(0);
  background: #efefef;
  width: 50%;
  height: 40%;
  padding: 45px;
  text-transform: uppercase;
  line-height: 2;
  transition: 0.5s all;
  border-radius: 22px;
  font-weight: 600;
}
.form__calculat-area {
  padding-bottom: 20px;
}
.form__calculat-close {
  width: 45px;
  height: 45px;
  margin-right: -50px;
  position: absolute;
  top: 0;
  right: 50px;
  background-image: url("../img/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: none;
  border-radius: 15px;
}

.active-one {
  display: block;
  pointer-events: all;
  transition: 0.5s all;
}

.active {
  transform: translate(-50%, -50%) scale(1);
  transition: 0.5s all;
}

.close__popup {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

